.product_type{
    padding: 20px 30px;
    width: 100%;
}
.product_type .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.product_type .container .box{
    padding: 10px 20px;
    margin-left: 10px;
}
.product_type .container .box .img_box{
    height: 200px;
    width: 200px;
    border-radius: 50%;
    background:#e8f4ff;
    overflow: hidden;
    cursor: pointer;
}
.product_type .container .box .img_box img{
    height: 80px;
    width: 80px;
    align-items: center;
    max-width: 100%;
    justify-content: center;
    margin-top: 50px;
    margin-left: 55px;
    transition: 0.5s;
}
.product_type .container .box .img_box:hover img{
    -webkit-transform:rotate(40deg);
            transform:rotate(40deg); 
}
.product_type .container .box .detail{
    max-width: 100%;
    text-align: center;
    align-items: center;
    
}
.product_type .container .box .detail p{
    margin-top: 10px;
    color:#9a9a9a;
}
.about{
    padding: 40px 50px;
    width: 100%;
}
.about .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.about .container .box{
    padding: 10px 20px;
    display: flex;
    background: #ecf2f7;
    width: 270px;
    height: 90px;
    border-radius: 10px;
    cursor: pointer;
}
.about .container .box .icon{
    color: #e44e8c;
    font-size: 32px;
    margin-top: 10px;
}
.about .container .box .detail{
    margin-top: 10px;
    margin-left: 10px;
}
.about .container .box .detail h3{
    color: #010f1c;
}
.about .container .box .detail p{
    color: #9a9a9a;
}
.product{
    padding: 30px 40px;
    width:100%;
}
.product h2{
    font-size: 32px;
    color: #010f1c;
    margin-bottom: 20px;
}
.product .container{
    display:flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.product .container .box{
    padding: 10px 20px;
    overflow: hidden;
    width: 280px;
    height:380px;
    border:1px solid #e2e0e0;
    margin-bottom:20px;
    transition: 0.5s;
    cursor: pointer;
}
.product .container .box:hover{
    box-shadow: rgba(0,0,0,0.35) 0px 3px 8px;
}
.product .container .box .img_box{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e2e0e0;
}
.product .container .box .img_box img{
    width: 300px;
    height: 250px;
    margin-right: 50px;
}
.product .container .box .img_box .icon{
    z-index: 1;
    position:relative;
    right:0;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    transition: 0.5s;
    
}
.product .container .box .img_box:hover .icon{
     -webkit-transform: translate(-130px);
             transform: translate(-130px); 
     /* when you place on product it shows cart add-item and wishlist from left sdie bcz of this and above right:0 and positioning:relative */
}
.product .container .box .img_box .icon li{
    list-style: none;
    padding: 10px 10px;
    color: #010f1c;
    cursor: pointer;
    box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
    background: #fff;
    transition: 0.5s;
    font-size: 18px;


}  
.product .container .box .img_box .icon li:hover{
    background: #0989ff;
    color: #fff;
}
.product .container .box .detail{
  margin-top: 10px;  
}
.product .container .box .detail p{
    color: #9a9a9a;
    font-size: 14px;

}
.product .container .box .detail h3{
    font-size: 16px;
    margin-top: 5px;
    color: #010f1c;
    transition: 0.5s;

}
.product .container .box:hover .detail h3{
    color:#0989ff;
}
.product .container .box .detail h4{
    margin-top: 5px;
    font-size: 14px;
    color: #0989ff;
    letter-spacing: 1px;
}
.banner{
    padding: 20px 30px;
    width:100%;
}
.banner .container{
    padding: 30px 40px;
    background: #0989ff;
    border-radius: 10px;
