*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.free{
    padding:10px 30px;
    width: 100%;
    background-color:#010f1c ;
    display: flex;

}
.free .icon{
    color:#f3b123 ;
    font-size: 32px;
}
.free p{
    margin-left: 10px;
    color: #f6f7f9;
    margin-top:8px;
    font-size: 14px;
    font-weight: 600;
}
.main_header{
    padding: 30px 40px;
    width:100%;
    border-bottom:1px solid #010f1c;

}
.main_header .container{
    display:flex ;
    justify-content: space-between;
}
.main_header .container .search_box{
    margin-right: 30px;

}
.main_header .container .search_box input{
    outline:none;
    padding: 10px 30px;
    border:3px solid #0989ff;
    width: 300px;
    color: #010f1c;
}
.main_header .container .search_box button{
    border: 3px solid #0989ff;
    outline: none;
    padding: 10px 20px;
    background-color: #0989ff;
    color: #ecf2f7;
}
.main_header .container .icon{
    display: flex;
    margin-right: 50px;

}
.main_header .container .acount{
    display: flex;
    margin-right: 30px;
}
.main_header .container .icon .acount .user_icon{
    margin-right: 10px;
    font-size: 22px;
    color: #010f1c;
    cursor: pointer;
}
.main_header .container .icon .acount  p{
    margin-top: 5px;
    margin-top: 2px;
}
.main_header .container .icon .second_icon{
    display: flex;

}


.main_header .container .icon .second_icon .link{
    margin-right: 5px;
    font-size:22px ;
    cursor: pointer;
    color: #0989ff;

}
.header{
    width:100%;
    padding: 20px 30px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
}
.header .container{
    display: flex;
    max-width: 100%;
    justify-content: space-between;
}

.header .container .nav ul{
    display: flex;
}
.header .container .nav ul li{
    margin-left: 20px;
    list-style:none;
}
.header .container .nav ul li .link{
    text-decoration: none;
    color: #010f1c;
}
.header .container .nav ul li .link:hover{
     color: #0989ff;
}
.header .container .auth{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
}
.header .container .auth button{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
    outline: none;
    border: none;
    background: none;
}